import { request } from 'utils';

const prefix = '/api/education';

export const getSubjectByFacultyAndSession = ({
  facultyId,
  registerSessionId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCredit/SubjectByFacultyAndSession',
    method: 'GET',
    params: {facultyId,registerSessionId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });



