import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getPlanSubjectDetail = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  studySessionId,
  fromWeek,
  toWeek,
  studyPeriodTypeId,
  subjectId,
  teacherId,
  roomId,
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
}) =>
  request({
    prefix,
    url: '/PlanSubjectDetail',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      studySessionId,
      fromWeek,
      toWeek,
      studyPeriodTypeId,
      subjectId,
      teacherId,
      roomId,
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {},
  });

export const createUpdatePlanSubjectDetails = (data) =>
  request({
    prefix,
    url: `/PlanSubjectDetail/CreateUpdatePlanSubjectDetails`,
    data,
    method: 'POST',
  });

export const getTeacherInPlanSubjectDetail = ({
  planSubjectDetails,
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
}) =>
  request({
    prefix,
    url: `/PlanSubjectDetail/GetTeacherInPlanSubjectDetail`,
    data: {
      planSubjectDetails,
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    method: 'POST',
  });

export const getStudyPeriodTypes = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/PlanSubjectDetail/StudyPeriodTypes',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

