import { request } from 'utils';

const prefix = '/api/categories';

export const getStudySessions = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/studySession',
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });






