import React, { Component } from 'react';
import { Icon, Form, Button } from 'antd';
import { Btn } from 'components/common/button';
import './style.scss';

const ButtonGroup = Button.Group;
@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <ButtonGroup>
        <Btn
          onClick={() => this.props.onExportPDF()}
        >
          <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
          In káº¿ hoáº¡ch
        </Btn>

        <Btn
          onClick={() => this.props.onExportExcel()}
        >
          <Icon type="file-excel" theme="twoTone" twoToneColor="green" />
          Xuáº¥t Excel
        </Btn>
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



