export const setFieldsValueCustom = (form, data) => {
  Object.keys(form.getFieldsValue()).forEach(key => {
    const obj = {};
    obj[key] = data[key] || null;
    if (obj[key]) form.setFieldsValue(obj);
  });
}

export const getFormTableData = (values, rowKey) => {
  const tempObj = {};
  Object.keys(values).forEach((value) => {
    const keyOfRow = value.split('-')[0];
    const id = value.split('-')[1];
    if (id) {
      if (!tempObj[id]) {
        tempObj[id] = {};
        if (rowKey) {
          tempObj[id].id = parseInt(id.split('_')[1]);
          tempObj[id][rowKey] = id;
        }
      }
      tempObj[id][keyOfRow] = values[value];
    }
  });
  return tempObj;
}

export const convertTableDataObjToArray = (obj) => {
  const newArray = [];
  Object.keys(obj).forEach((value) => {
    newArray.push(obj[value]);
  });
  return newArray;
}


