import React, { Component } from 'react';
import {
  Input,
  Form,
  Table
} from 'antd';
import { SelectAjax } from 'components/common';
import { getStudyPeriodTypes } from 'services/planSubjectDetailâ€‹';
import { getStudySessions } from 'services/studySession';
import { getStaffs } from 'services/staff';
import { getBuildingRooms } from 'services/buildingRoom';

import './style.scss';

@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {

  handleData = () => {
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('values: ', values);
      }
    });
  }

  render() {
    const { data, reload, rowKey } = this.props;
    const columns = [
      {
        title: 'TÃªn lá»›p',
        dataIndex: 'className',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`className-${record.autoKey}`, { initialValue: record.className })(
                <span>{record.className}</span>
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'TÃªn mÃ´n',
        dataIndex: 'subjectLabel',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`subjectLabel-${record.autoKey}`, { initialValue: record.subjectLabel })(
                <span>{record.subjectLabel}</span>
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Sá»‘ há»c trÃ¬nh',
        dataIndex: 'noOfCredit',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`noOfCredit-${record.autoKey}`, { initialValue: record.noOfCredit })(
                <span>{record.noOfCredit}</span>
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Lá»›p ghÃ©p',
        dataIndex: 'ClassSubjectConcat',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`classSubjectConcat-${record.autoKey}`, { initialValue: record.classSubjectConcat })(
                <span>{record.classSubjectConcat === '' ? 'KhÃ´ng cÃ³' : record.classSubjectConcat}</span>
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Loáº¡i giá»',
        dataIndex: 'studyPeriodTypeId',
        render: (_text, record) => {
          const {
            getFieldDecorator,
            setFieldsValue,
          } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`studyPeriodTypeId-${record.autoKey}`, { initialValue: record.studyPeriodTypeId })(
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getStudyPeriodTypes}
                  typeValue="int"
                />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'S tiáº¿t CTÄT',
        dataIndex: 'noOfPeriodProgram',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`noOfPeriodProgram-${record.autoKey}`, { initialValue: record.noOfPeriodProgram })(
                <span>{record.noOfPeriodProgram}</span>
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Chia tiáº¿t',
        dataIndex: 'splitPeriod',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`splitPeriod-${record.autoKey}`, {
                initialValue: record.splitPeriod,
                rules: [
                  {
                    required: true,
                    message: 'Vui lÃ²ng Ä‘iá»n Chia tiáº¿t!',
                    pattern: new RegExp("^\\d\\-\\d$|^\\d$"),
                  },
                ],
              })(
                <Input style={{ width: '75px' }} />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Ca há»c',
        dataIndex: 'studySessionId',
        render: (_text, record) => {
          const {
            getFieldDecorator,
          } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`studySessionId-${record.autoKey}`, { initialValue: record.studySessionId })(
                <SelectAjax
                  service={getStudySessions}
                />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Giáº£ng viÃªn',
        dataIndex: 'teacherId',
        render: (_text, record) => {
          const {
            getFieldDecorator,
            setFieldsValue,
          } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`teacherId-${record.autoKey}`, { initialValue: record.teacherId })(
                <SelectAjax
                  keyValue="staffId"
                  keyLabel="fullname"
                  form={this.props.form}
                  setFieldsValue={setFieldsValue}
                  service={getStaffs}
                  allowGetObjSelected
                />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'PhÃ²ng há»c',
        dataIndex: 'roomId',
        render: (_text, record) => {
          const {
            getFieldDecorator,
            setFieldsValue
          } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`roomId-${record.autoKey}`, { initialValue: record.roomId })(
                <SelectAjax
                  keyValue="building"
                  keyLabel="buildingBase"
                  form={this.props.form}
                  setFieldsValue={setFieldsValue}
                  service={getBuildingRooms}
                  allowGetObjSelected
                />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Tá»« tuáº§n',
        dataIndex: 'fromWeek',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`fromWeek-${record.autoKey}`, { initialValue: record.fromWeek })(
                <Input style={{ width: '75px' }} />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Äáº¿n tuáº§n',
        dataIndex: 'toWeek',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`toWeek-${record.autoKey}`, { initialValue: record.toWeek })(
                <Input style={{ width: '75px' }} />
              )}
            </Form.Item>
          );
        },
      },
    ];
    const rowSelection = {
      onChange: (_selectedRowKeys, selectedRows) => {
        this.props.handleSelectedRows(selectedRows);
      },
    };
    return (
      <Table
        reload={reload}
        columns={columns}
        dataSource={data}
        rowSelection={rowSelection}
        scroll={{ x: true }}
        rowKey={rowKey}
      />
    );
  }
}

export default TrainingPlanTableDetail;



