import React, { Component } from 'react';
import {
  Card,
  Row,
  Col,
  message,
  Spin,
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import {
  getPlanSubjectDetail,
  createUpdatePlanSubjectDetails,
  getTeacherInPlanSubjectDetail,
  exportPlanSubjectDetailExcel,
  exportPlanSubjectDetailPdf,
} from 'services/planSubjectDetailâ€‹';
import { makeid } from 'utils/functionUtil';
import { getFormTableData, convertTableDataObjToArray } from 'utils/formUtil';

import SearchBarTrainingPlan from './FormTrainingPlan';
import MenuTabTop from './MenuTabTop';
import TrainingPlanTableDetail from './TrainingPlanTableDetail';
import './style.scss';

class TrainingPlanTable extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataTraningPlan: null,
      selectedRows: null,
      rowKey: "autoKey",
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getTableData = (values) => {
    const { rowKey } = this.state;
    const formDataObj = getFormTableData(values, rowKey);
    const formDataArray = convertTableDataObjToArray(formDataObj);
    return formDataArray;
  }

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.courseId &&
        !!paramSearch.studyFieldId &&
        !!paramSearch.yearEduId &&
        !!paramSearch.semesterId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getPlanSubjectDetail(paramSearch);
        let tempData = [...response.data.items];
        tempData = tempData.map(item => {
          item.autoKey = `${makeid(8)}_${item.id}`;
          return item;
        })
        this.setState({
          dataTraningPlan: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  handleSave = () => {
    try {
      this.refForm.current.validateFields(async (err, values) => {
        if (!err) {
          this.setState({
            loading: true,
          });
          const { selectedRows, rowKey } = this.state;
          if (selectedRows && selectedRows.length) {
            const formDataArray = this.getTableData(values);
            const listSelectedKey = selectedRows.map(item => {
              return item[rowKey];
            });
            const listSelectedItem = formDataArray.filter(item => listSelectedKey.includes(item[rowKey]));
            const formatDataToSave = {
              planSubjectDetails: [...listSelectedItem],
            }
            await createUpdatePlanSubjectDetails(formatDataToSave);
            this.setState({
              dataTraningPlan: null,
              selectedRows: null,
            });
            message.success('XÃ³a thÃ nh cÃ´ng');
          }
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleAuto = async () => {
    try {
      this.setState({
        loading: true,
        selectedRows: null,
      });
      this.refForm.current.validateFields(async (err) => {
        if (!err) {
          // const formDataArray = this.getTableData(values);
          const { dataTraningPlan } = this.state;
          const formatDataToAuto = {
            planSubjectDetails: [...dataTraningPlan],
          }
          const response = await getTeacherInPlanSubjectDetail(formatDataToAuto);
          let tempData = [...response.data.items];
          tempData = tempData.map(item => {
            item.autoKey = `${makeid(8)}_${item.id}`;
            return item;
          })
          this.setState({
            dataTraningPlan: [...tempData],
          });
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleMerge = () => { 
    console.log('on merge');
  }

  handleExportPDF = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailPdf(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailExcel(paramSearch);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    const { dataTraningPlan, reload, rowKey, loading } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title="PhÃ¢n bá»• giÃ¡o viÃªn, ca há»c, phÃ²ng há»c">
          <Row>
            <Col span={24}>
              <CartHeadAction>
                <MenuTabTop
                  onExportExcel={this.handleExportExcel}
                  onExportPDF={this.handleExportPDF}
                />
              </CartHeadAction>
            </Col>
            <Col span={24}>
              <SearchBarTrainingPlan
                onSave={this.handleSave}
                onAuto={this.handleAuto}
                onMerge={this.handleMerge}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TrainingPlanTableDetail
                reload={reload}
                data={dataTraningPlan}
                ref={this.refForm}
                handleSelectedRows={this.handleSelectedRows}
                rowKey={rowKey}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



