import { request } from 'utils';

const prefix = '/api/organization';

export const getStaffs = ({
  fullname,
  gender,
  facultyId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Staff',
    method: 'GET',
    params: { fullname, gender, facultyId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



