import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { BtnSearch, BtnSave, Btn } from 'components/common/button';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { getClasss } from 'services/class';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getStudySessions } from 'services/studySession';
import { getSubjectByFacultyAndSession } from 'services/classCredit';
import { getSubjectGroups } from 'services/subjectGroup';
import { getStaffs } from 'services/staff';
import { getBuildingRooms } from 'services/buildingRoom';
import { getStudyPeriodTypes } from 'services/planSubjectDetailâ€‹';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';

@Form.create({ name: 'trainingPlan' })
class SearchBarTrainingPlan extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        udpateQueryParams(values);
      }
    });
  };

  render() {
    const { form } = this.props;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const yearEduId = getFieldValue('yearEduId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const rules = [
      {
        required: true,
        message: 'Vui lÃ²ng chá»n Há»‡!',
      },
    ];
    const optionFieldDecorator = {
      trainTypeId: {
        rules,
      },
      facultyId: {
        rules,
      },
      courseId: {
        rules,
      },
      studyFieldId: {
        rules,
      },
      yearEduId: {
        rules,
      },
      semesterId: {
        rules,
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Há»‡">
              {getFieldDecorator('trainTypeId',
                optionFieldDecorator.trainTypeId
              )(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Khoa">
              {getFieldDecorator(
                'facultyId',
                optionFieldDecorator.facultyId
              )(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="KhoÃ¡">
              {getFieldDecorator(
                'courseId',
                optionFieldDecorator.courseId
              )(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getCourses}
                  params={{ trainTypeId, facultyId }}
                  allowGetData={!!facultyId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator(
                'studyFieldId',
                optionFieldDecorator.studyFieldId
              )(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId }}
                  allowGetData={!!courseId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lá»›p">
              {getFieldDecorator(
                'classId'
              )(
                <SelectAjax
                  typeValue="int"
                  service={getClasss}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                  allowGetData={!!studyFieldId}
                // open={!!studyFieldId && !classId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="NÄƒm há»c">
              {getFieldDecorator('yearEduId',
                optionFieldDecorator.yearEduId
              )(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Há»c ká»³">
              {getFieldDecorator('semesterId',
                optionFieldDecorator.semesterId
              )(
                <SelectAjax
                  service={getSemesters}
                  setFieldsValue={setFieldsValue}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ca há»c:">
              {getFieldDecorator('studySessionId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getStudySessions}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Bá»™ mÃ´n">
              {getFieldDecorator('subjectGroupId', {
                // initialValue: profile.subjectGroupId,
              })(
                <SelectAjax service={getSubjectGroups} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Tá»« tuáº§n">{getFieldDecorator('fromWeek', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Äáº¿n tuáº§n">{getFieldDecorator('toWeek', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loáº¡i giá»">
              {getFieldDecorator('studyPeriodTypeId', {
                // initialValue: profile.subjectGroupId,
              })(
                <SelectAjax service={getStudyPeriodTypes} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Há»c pháº§n">
              {getFieldDecorator('subjectId', {
                // rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSubjectByFacultyAndSession}
                  params={{ facultyId }}
                  allowGetData={!!facultyId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="GiÃ¡o viÃªn">
              {getFieldDecorator('teacherId', {
                // initialValue: record.teacherId,
              })(
                <SelectAjax
                  keyValue="staffId"
                  keyLabel="fullname"
                  form={form}
                  setFieldsValue={setFieldsValue}
                  service={getStaffs}
                  allowGetObjSelected
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="PhÃ²ng há»c">
              {getFieldDecorator('roomId',
                // optionFieldDecorator.roomId
              )(
                <SelectAjax
                  keyValue="building"
                  keyLabel="buildingBase"
                  form={form}
                  service={getBuildingRooms}
                  setFieldsValue={setFieldsValue}
                  allowGetObjSelected
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnSave style={{ marginRight: 0 }} onClick={() => this.props.onSave()} />
              <Btn
                // style={{ marginLeft: 20, height: 35 }}
                // onClick={this.exportAddReward}
                icon="diff"
                onClick={() => this.props.onMerge()}
              >
                GhÃ©p Lá»›p
              </Btn>
              <Btn
                // style={{ marginLeft: 20, height: 35 }}
                // onClick={this.exportAddReward}
                icon="thunderbolt"
                onClick={() => this.props.onAuto()}
              >
                PhÃ¢n GiÃ¡o ViÃªn Tá»± Äá»™ng
              </Btn>
              {/* <BtnClear /> */}
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarTrainingPlan;



